import React from 'react';
import NavbarSingle from "./NavbarSingle";
import NavFriends from "./NavFriends";
import Navbar from "./Navbar";
import {connect} from "react-redux";

let mapStateToProps = (state) => {
    return {
        sideNav: state.sidebar.barNav
            .map(post => <NavbarSingle name={post.name} link={post.link} key={post.name}/>),

        navFriends: state.sidebar.navFriends
            .map(elem => <NavFriends name={elem.name} key={elem.name}/>)

    }
}

let mapDispatchToProps = (dispatch) => {
    return {}
}

const NavbarContainer = connect(mapStateToProps, mapDispatchToProps)(Navbar);


export default NavbarContainer;