import React from 'react';
import css from './Navbar.module.css';

const Navbar = (props) => {
    return (
        <div className={css.sidebar}>
            <ul className={css.navbar}>
                { props.sideNav }
            </ul>
            <ul className={css.friendsName}>
                { props.navFriends }
            </ul>
        </div>
    );
}

export default Navbar;