import React from "react";
import css from "./Users.module.css";
import userPhoto from './../../assets/image/ReactiveLogo.png'
import {NavLink} from "react-router-dom";

let SingleUser = ({followingInProgress, follow, unfollow,
                      user: {
                          id,
                          name,
                          status,
                          followed,
                          photos: {
                              small
                          }
                      }
                  }) => {

    return <div key={id}>
                    <span>
                        <div className={css.posts}>
                            <NavLink to={'/profile/' + id}>
                                <img src={small != null ? small : userPhoto}/>
                            </NavLink>
                        </div>
                        <div className={css.postsInput}>
                            {followed
                                ? <button disabled={followingInProgress.some(id => id === id)} onClick={() => {
                                    unfollow(id)
                                }}>Unfollow</button>
                                : <button disabled={followingInProgress.some(id => id === id)} onClick={() => {
                                    follow(id)
                                }}>Follow</button>}
                        </div>
                    </span>
        <span>
                        <span>
                            <div>{name}</div>
                            <div>{status}</div>
                        </span>
                        <span>
                            <div>{'user.location.city'}</div>
                            <div>{'user.location.country'}</div>
                        </span>
                    </span>
    </div>
}

export default SingleUser