const SEND_MESSAGE = 'SEND_MESSAGE';

let initialState = {
    dialogData: [
        {id: 1, name: 'Dimych'},
        {id: 2, name: 'Valera'},
        {id: 3, name: 'Vanya'},
        {id: 4, name: 'Misha'},
        {id: 5, name: 'Masha'},
        {id: 6, name: 'Nastya'},
        {id: 7, name: 'Vlad'},
    ],
    messageData: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'Oh shit'},
        {id: 3, message: 'Really niga?'},
    ],
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
            return {
                ...state,
                messageData:[...state.messageData, {id: 4, message: body}]
            };
        default:
            return state;
    }
}

export const sendMessageActionCreator = (newMessageBody) => ({
    type: SEND_MESSAGE, newMessageBody
})

export default dialogsReducer;